.main-slider {
  position: relative;
  width: 50%;
  max-width: 540px;
  .slide {
    width: 100%;
    height: 490px;
    position: relative;
    &__img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &__content {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      max-width: 290px;
      width: 100%;
    }
  }
  .arrow {
    position: absolute;
    top: 50%;
    transform: translate(0, -50%) rotate(0deg);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background-color: #fff;
    opacity: 60%;
    border-radius: 50%;
    cursor: pointer;
    &-prev {
      left: 24px;
    }
    &-next {
      right: 24px;
      transform: translate(0, -50%) rotate(180deg);
    }
  }

}